.cv-stage-card {
  position: relative;
  width: 100%;
  flex-shrink: 0;
  height: auto;
  min-height: 500px;
  --headline-font: 'Open Sans', sans-serif;
  --font: 'Open Sans', sans-serif;
  --font-size: 19px;
  display: flex; }
  .cv-stage-card__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-size: cover;
    --brightness: 95%;
    filter: brightness(var(--brightness)) contrast(85%) saturate(98%); }
  .cv-stage-card__overlay {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), radial-gradient(circle at bottom left, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0) 80%); }
  .cv-stage-card__body {
    margin-left: calc(30px + 5%);
    margin-right: calc(30px + 5%);
    margin-top: 90px;
    margin-bottom: 80px;
    color: #fff; }
  .cv-stage-card__title-wrapper {
    min-height: 140px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 20px;
    max-width: 450px;
    width: 100%; }
  .cv-stage-card h2, .cv-stage-card__title {
    --title-font-size-factor: 1;
    position: relative;
    white-space: pre;
    font-weight: bold;
    font-size: calc(40px * var(--title-font-size-factor)) !important;
    font-family: var(--headline-font);
    line-height: 1 !important;
    text-transform: uppercase;
    margin: 0;
    white-space: pre-line;
    color: inherit; }
    .cv-stage-card h2::before, .cv-stage-card__title::before {
      content: '';
      position: absolute;
      top: 4px;
      bottom: 2px;
      left: -30px;
      width: 8px;
      background: #d22;
      margin: 0;
      height: auto; }
    @media (max-width: 500px) {
      .cv-stage-card h2, .cv-stage-card__title {
        font-size: calc(30px * var(--title-font-size-factor)) !important; }
        .cv-stage-card h2::before, .cv-stage-card__title::before {
          left: -25px; } }
    @media (max-width: 350px) {
      .cv-stage-card h2, .cv-stage-card__title {
        font-size: calc(25px * var(--title-font-size-factor)) !important; }
        .cv-stage-card h2::before, .cv-stage-card__title::before {
          left: -20px; } }
  .cv-stage-card__description {
    position: relative;
    font-family: var(--font);
    font-size: var(--font-size);
    max-width: 300px;
    margin-right: 20px;
    width: 100%;
    color: inherit;
    line-height: 1.7em;
    -webkit-font-smoothing: subpixel-antialiased; }
    @media (max-width: 500px) {
      .cv-stage-card__description {
        font-size: calc(0.95 * var(--font-size)) !important; } }
    @media (max-width: 350px) {
      .cv-stage-card__description {
        font-size: calc(0.9 * var(--font-size)) !important; } }
  .cv-stage-card__link {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-items: end; }
