.wp-block-cv-blocks-cv-stage {
  margin-top: 0 !important; }

stage-slider .swiper-container {
  width: 100%;
  height: 100%; }

.cv-stage__item {
  width: 100%;
  background: #d2182e; }

.cv-stage__controls {
  position: absolute;
  z-index: 10;
  bottom: 10px;
  left: calc(30px + 5%);
  color: #fff;
  display: flex;
  align-items: center; }
  .cv-stage__controls .swiper-pagination {
    position: relative;
    padding: 0 4px 8px 4px; }
    .cv-stage__controls .swiper-pagination > * {
      margin: 0 2px; }
  .cv-stage__controls .swiper-button-prev--custom,
  .cv-stage__controls .swiper-button-next--custom {
    padding: 0 4px; }
  .cv-stage__controls .swiper-button-disabled {
    opacity: 0.2; }

.swiper-pagination-bullet {
  background: #f2f2f2;
  opacity: 0.3; }

.swiper-pagination-bullet-active {
  background: #f2f2f2;
  opacity: 1; }
